___CSS_LOADER_EXPORT___.push([module.id, ".nav-link{color:#f08b23}.nav-link:hover{color:#0d274b}", "",{"version":3,"sources":["webpack://./resources/js/desktop/pages/Manager/Messages/Index.vue"],"names":[],"mappings":"AAwFA,UACA,aACA,CACA,gBACA,aACA","sourcesContent":["<template>\n    <b-container fluid>\n        <b-tabs content-class=\"mt-3 w-100\">\n            <b-tab title=\"Все\" active>\n                <messages-table\n                    :messages=\"messages\"\n                    :loading=\"loading\"\n                    :totalRows=\"messagesTotalRows\"\n                />\n            </b-tab>\n            <b-tab title=\"Удаленные\">\n                <messages-table\n                    :messages=\"deleted_messages\"\n                    :loading=\"loading\"\n                    :totalRows=\"deletedMessagesTotalRows\"\n                />\n            </b-tab>\n        </b-tabs>\n    </b-container>\n</template>\n\n<script>\n    import MessagesTable from '@/js/desktop/pages/Manager/Messages/Table'\n    export default {\n        components: {\n            MessagesTable,\n        },\n        data() {\n            return {\n                loading: false,\n                messagesTotalRows: 0,\n                deletedMessagesTotalRows: 0,\n            }\n        },\n        computed: {\n            menu() {\n                return this.$store.getters.menu\n            },\n            isAdmin : function(){ return this.$store.getters.isAdmin},\n            isManager : function(){ return this.$store.getters.isManager},\n            messages() {\n                return this.$store.getters.messages\n            },\n            deleted_messages() {\n                return this.$store.getters.deleted_messages\n            },\n\n        },\n        created() {\n            if (this.messages.length<=0) {\n                this.setMessages();\n            }\n        },\n        mounted() {\n\n        },\n        methods: {\n            async setMessages(){\n                this.loading=true;\n                if(this.messages.length<=0){\n                    await this.$store.dispatch('setMessages').then(() => {\n                        this.messagesTotalRows = this.messages.length;\n                        this.deletedMessagesTotalRows = this.deleted_messages.length;\n                        this.loading = false;\n                    })\n                }\n                this.loading = false;\n            },\n            verifyManager(id){\n                this.$store.dispatch('saveUser', {id:id, value:1, key:'is_manager'})\n            },\n            unverifyManager(id){\n                this.$store.dispatch('saveUser', {id:id, value:0, key:'is_manager'})\n            },\n            sendMessage(message) {\n\n                this.$notify({\n                    group: 'info',\n                    type: 'travel',\n                    title: 'Сообщение от ВПУТЬ',\n                    text: message\n                });\n            },\n        }\n    }\n</script>\n\n<style>\n.nav-link {\n   color: #f08b23;\n}\n.nav-link:hover {\n   color: #0d274b;\n}\n</style>\n"],"sourceRoot":""}]);
