t\n                            v-model=\"filter\"\n                            type=\"search\"\n                            id=\"filterInput\"\n                            placeholder=\"Введите для поиска\"\n                        />\n                        <b-input-group-append>\n                            <b-button :disabled=\"!filter\" @click=\"filter = ''\">Очистить</b-button>\n                        </b-input-group-append>\n                    </b-input-group>\n                </b-form-group>\n            </b-col>\n\n            <b-col sm=\"5\" md=\"6\" class=\"my-1\">\n                <b-form-group\n                    label=\"На странице\"\n                    label-cols-sm=\"6\"\n                    label-cols-md=\"4\"\n                    label-cols-lg=\"3\"\n                    label-align-sm=\"right\"\n                    label-size=\"sm\"\n                    label-for=\"perPageSelect1\"\n                    class=\"mb-0\"\n                >\n                    <b-form-select\n                        v-model=\"perPage\"\n                        id=\"perPageSelect1\"\n                        size=\"sm\"\n                        :options=\"pageOptions\"\n                    />\n                </b-form-group>\n            </b-col>\n\n            <b-col sm=\"7\" md=\"6\" class=\"my-1\">\n                <b-pagination\n                    v-model=\"currentPage\"\n                    :total-rows=\"totalRows\"\n                    :per-page=\"perPage\"\n                    align=\"fill\"\n                    size=\"sm\"\n                    class=\"my-0\"\n                >\n                </b-pagination>\n            </b-col>\n        </b-row>\n        <b-table\n            show-empty\n            small\n            stacked=\"md\"\n            :items=\"messages\"\n            :fields=\"fields\"\n            :current-page=\"currentPage\"\n            :per-page=\"perPage\"\n            :filter=\"filter\"\n            :filterIncludedFields=\"filterOn\"\n  